import logoImg from './images/cloud-logo.png';

'use strict';

export default class commonMenuController {
	constructor($scope, $state) {
		"ngInject";
		this.$scope = $scope;
		this.$state = $state;
		this.logoImg = logoImg;
	}

	collapse(e) {
		$(e.target).siblings('.sub-menu').toggle();
	}
}



